import React, { Component } from 'react';
import './App.css';
import { Container } from 'react-bootstrap';

class Home extends Component {
  state = {
    urlhistory: ''
  };

  onSocials(oEvent) {
    oEvent.target.className.indexOf('git') > 0
      ? window.open('https://github.com/olayenca', '_blank')
      : oEvent.target.className.indexOf('linkedin') > 0
      ? window.open('https://www.linkedin.com/in/olayinka-otuniyi/', '_blank')
      : oEvent.target.className.indexOf('twitter') > 0
      ? window.open('https://twitter.com/tinker_yenca', '_blank')
      : window.open('https://www.instagram.com/tinker_yenca/', '_blank');
  }

  componentDidUpdate() {
    this.props.history.replace('/home', 'urlhistory');
  }
  render() {
    return (
      <Container>
        <div id="react_intro" className="row centered">
          <div style={{ width: '46vw' }}>
            <div id="engineer_jumbo" className="row-sm-12">
              <i className="fas fa-cogs" />
              <p id="jumbo_0">Engineer</p>
            </div>

            <p id="jumbo_1" className="row-sm-12">
              &lt;\Developer&gt;
            </p>
            <p id="react_intro_text" className="row-sm-12">
              An experienced Software Engineer with a self-motivated and
              enthusiastic attitude towards turning concepts, ideas and
              theoretical innovation into real life working projects, through
              the programming of user-friendly and reliable applications.
            </p>
          </div>{' '}
          <div
            id="socials"
            style={{
              display: 'flex',
              justifyContent: 'space-evenly',
              fontSize: '5vmin'
            }}
          >
            <div
              onClick={this.onSocials}
              className="icons"
              id="git"
              style={{ marginRight: '.7vw' }}
            >
              <i className="fab fa-github-alt" />
            </div>
            <div onClick={this.onSocials} className="icons" id="linkedin">
              <i className="fab fa-linkedin-in" />
            </div>
            <div onClick={this.onSocials} className="icons" id="twitter">
              <i className="fab fa-twitter" />
            </div>
            <div onClick={this.onSocials} className="icons" id="insta">
              <i className="fab fa-instagram" />
            </div>
          </div>
        </div>
      </Container>
    );
  }
}

export default Home;
